import React from 'react';
import {HashRouter as Router,Redirect,Route,NavLink,Switch,privateRoute} from 'react-router-dom'
import { Provider } from 'react-redux';
import 'antd-mobile/dist/antd-mobile.css';
import Home from './Home';
import Collect from './Shoucang';
import List from './List';
import My from './My';
import Mytabs from './Mytabs';
import SearchBarExample from './SearchBar';
import Detail from './detail';
import User from './userinfo';
import store from './Store';

function PrivateRoute({ component: Com, ...rest }) {
	return (
		<Route
			{ ...rest }
			render={ ({ location }) =>
				localStorage.getItem('userinfo')
					? <Com {...rest}/>
					: (
						<Redirect
							to={ {
								pathname: "/home/login",
								state: location
							} }
						/>
					)
			}
		/>
	);
}
const App = ()=>{
	return <Provider store={store}>
	<Router>
		<Switch>
			<Route exact path='/' component={Home } />
			<PrivateRoute path='/shoucang' component={Collect} />
            <Route path='/list' component={List} />
            <Route path='/my' component={My} />
			{/* <Route component={Home} /> */}
			<Route path='/home/searchbar' component={SearchBarExample} />
			<Route path='/home/detail' component={Detail} />
			<Route path='/home/login' component={User} /> 
			{/* <Route render={()=><Redirect to='/' />} /> */}
		</Switch>
		<Mytabs />
	</Router>
	</Provider>
}
export default App;

