import React, { Component } from 'react'
//import MytabDetail from './MytabDetail'
import {HashRouter as Router,Redirect,Route,NavLink,Switch,privateRoute} from 'react-router-dom'
import {NavBar,Icon, ActionSheet} from 'antd-mobile'
import 'antd-mobile/dist/antd-mobile.css';
import {Button,TabBar} from 'antd-mobile';
import {connect} from 'react-redux';
import './detail.css'
class Detail extends Component {
    constructor(props) {
        super(props);
        this.state = {
          selectedTab: 'dianpu',
        };
      }
    componentDidMount(){
		//判断是否被收藏
        let id = this.props.location.id; 
        this.props.collectdata.map((item)=>{
            if(item.id===id){
                this.setState({
                    selectedTab: 'detail',
                  });
        }
     })
    }

    render() {
        const {pathname} =this.props.location;
        let img = this.props.location.src;
        let id = this.props.location.id;
        let src='';
        console.log(this.props)
        return (
            <div>
                <NavBar
                        mode="light"
                        icon={<Icon type="left" />}
                        onLeftClick={() => this.props.history.push('/')}
                    >商品详情</NavBar>
                 <img src={img} style={{width
          :'100%'}} />
         
            <Router>
                {/* <MytabDetail /> */}
                <div style={{ position: 'fixed', width: '100%', bottom:0 }}>
		  <TabBar
			unselectedTintColor="#949494"
			tintColor="rgb(255, 92, 18)"  //选中字体颜色
			barTintColor="white"  //背景颜色
		  >
			<TabBar.Item
			  icon={<i className='iconfont icon-dianpu'></i>}
			  selectedIcon={<i className='iconfont icon-dianpu'></i>}
			  title="店铺"
			  key="dianpu"
			//   selected={pathname === '/dianpu'}
			  onPress={() => {
				console.log('dianpu')
			  }
			  }
			  data-seed="logId"
			>
			</TabBar.Item>
			<TabBar.Item
			  icon={<i className='iconfont icon-kefu'></i>}
			  selectedIcon={<i className='iconfont icon-kefu'></i>}
			  title="客服"
			  key="kefu"
			//   selected={pathname === '/kefu'}
			  onPress={() => {
				console.log('kefu')
			  }
			  }
			  data-seed="logId1"
			>
			</TabBar.Item>
			<TabBar.Item
			  icon={ <i className='iconfont icon-shoucang'></i>}
			  selectedIcon={<i className='iconfont icon-shoucang1'></i>}
			  title="收藏"
			  key="shoucang"  
			  selected={this.state.selectedTab === 'detail'}
			  onPress={() => {
                if(localStorage.getItem('userinfo')){
				    this.props.dispatch({
					    type:"ADD_LIST",
                        value:{img:img,id:id}
                    })
            
                    this.setState({
                        selectedTab: 'detail',
                    });
                }else{
					// this.props.dispatch({
					//     type:"ADD_LIST",
                    //     value:{img:img,id:id}
                    // })
            
                    // this.setState({
                    //     selectedTab: 'detail',
                    // });
					this.props.history.push('/home/login')
                }
                        
			  }}
			>
			</TabBar.Item>
			<TabBar.Item
			  icon={<button className="left">加入购物车</button>}
			  key="jiagou"  
			>
			</TabBar.Item>
			<TabBar.Item
			  icon={<button className="right">立即购买</button>}
			  key="buy"  
			>
			</TabBar.Item>
		  </TabBar>
		</div>
            </Router>
          
		</div>
        )
    }
}
const mapStateToProps = (state) =>({
    collectdata:state.shoucang
})
export default connect(mapStateToProps)(Detail)