import {ActivityIndicator} from 'antd-mobile'
import React,{useState} from 'react'
import {connect} from 'react-redux';
const Collect = (props) => {
    console.log(props.collectdata)
    return (
        <ul>
           {
               props.collectdata.map((item,idx)=>
                <li key={item.id}>
                <img src={item.img} style={{width:'50%'}}/>
                <button onClick={(e)=>{
                     props.dispatch({
                        type:'DELETE_LIST',
                        index:idx
                    })
                }}>删除</button></li>
               )
           }
        </ul>
    )
}
//把状态映射到属性当中去,dispatch派发action更新状态
const mapStateToProps = (state) =>({
    collectdata:state.shoucang
})
export default connect(mapStateToProps)(Collect)
