let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/';
const login = (username,props)=>{
    return (dispatch)=>{
        fetch(url+'login',{
            method:'post',
            headers:{
                'Content-Type':'application/json'
            },
        })
        .then(res=>res.json())
        .then(res=>{
            dispatch({
                type:'LOGIN',
                username:res.data.name
              
            })
        });
    }
}

const goodsList = (goods,props)=>{
    return (dispatch)=>{
        fetch(url+'getgoodslist',{
            method:'get',
            headers:{
                'Content-Type':'application/json'
            },
        })
        .then(res=>res.json())
        .then(res=>{
            dispatch({
                type:'GOODS',
                goods:res.data
              
            })
        });
    }
}
export {login}
export {goodsList}

